.MainInput {
  display: flex;
  flex: 0 1 690px;
  margin: 0 7px;
  background: var(--background-color);
  position: relative;

  border: 1px solid var(--border-color);
  border-radius: 2px;
  padding: 8px 12px 8px 10px;
  gap: 4px;

  @media (max-width: 600px) {
    position: absolute;

    left: 0;
    margin: 0px;
    top: 65%;
    width: 100%;
    box-sizing: border-box;
  }

  &__input {
    border: none;
    flex: 0 1 100%;
    outline: none;
    background-color: var(--background-color);
    color: var(--text-color-secondary);
    &::placeholder {
      /* Chrome, Firefox, Opera, Safari 10.1+ */
      color: var(--text-color-secondary);
      opacity: 75%; /* Firefox */
    }
  }

  &__icon {
    width: 20px;
    height: 20px;
    margin-top: 2px;
  }
}
